/* EventDetails.css */

.event-details-container {
    position: fixed;
    top: 0;
    left: 100%; /* Initially off-screen on the right */
    width: 200%; /* Twice the width to cover the entire screen */
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: row; /* Stack the components horizontally */
    transition: left 0.3s ease; /* Add a smooth transition */
  }
  
  .event-details-container.slide-left {
    left: 0; /* Slide in to the screen */
  }
  
  .event-details {
    flex: 1; /* Occupy remaining space */
  }
  
